<template>
    <div class="openAccount" >
      <dialogs type="customer" ref="openAccount" v-if="showDialog" :width="!showData ? '30em' : '47em'" overflow="auto">
        <div slot="header">
          {{operateFlag === 1 ? 'IC': 'IC'}}
        </div>
        <div slot="content">
          <p style="padding:20px" v-if="!showData">{{!showAlerts ? '...' : msg}}</p>
          <ul class="form-list dialog-ul" v-if="showData" style="padding: 0 3em; width: calc(100% - 6em);">
            <li  :class="item.classes || ''" v-for="(item, $index) in dialogFrom ">
              <template v-if="item.type !== 'button' && item.show" class="item.classes">
                <label class="label-span" style="line-height: 1.8;min-width: 5.5em">{{item.label}} :&nbsp;&nbsp;</label>
                <label class="label-span" style="line-height: 1.8;">{{item.value}}</label>
              </template>
            </li>
            <li style="margin:0" class="allWidth" v-show="operateFlag !== 3">
              <priceTable v-if="showPrice"  :priceData="priceData"></priceTable>
            </li>
            <li  v-if="operateFlag === 3" style="margin:0 0.2em; width:100%">
              <p class="max-length" style="text-align:left;margin:0 0 1em 0">{{payMsg}} </p>
              <table class="table-list" style="width: 100%;margin:0;">
                <tr class="table-header">
                  <td style="width: 150px"></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td>{{customerInfo.payDate}}</td> 
                  <td>{{customerInfo.payment}}</td>
                  <td>{{customerInfo.payMoney}}</td>
                  <td>{{customerInfo.payCount}}</td>
                </tr>
              </table>
            </li>
          </ul>
        </div>
        <div slot="footer" >
          <template v-if="showData">
            <ul class="form-list dialog-ul" v-if="showData" style="padding: 1em 2em 0 2em; width:calc(100% - 4em)">
              <template v-for="(items, $indexs) in inputOpt"
                        v-if="items.isShow && customerInfo.meterType != 0">
                <li :class="items.classes">
                  <label class="label-span" style="line-height:2.5;min-width:5em">{{items.name}}&nbsp;&nbsp;</label>
                  <inputs :ref="items.option.key" :option="items.option"
                          @RealData="getForm"  @getInput="getForm"></inputs>
                </li>
              </template>
              <li class="halfWidth hasMarginLi" style="line-height: 2.5;">
                <template v-if="operateFlag != 3 && customerInfo.meterType != 0">
                  <label class="max-length" style="width:300px;text-align: left;float: left;font-size:0.83em">   =  - </label>
                </template>
              </li>
              <li class="allWidth" >
                <flash style="margin-left:-1em"></flash>
              </li>
            </ul>
            <template v-if="customerInfo.meterType != 0">
              <div class="footerBtn">
                <button :disabled="allowWrite!=2" class="button-default"
                        @click="readCardClick">
                  
                </button>
                <button class="button-default cancel-button" @click="close"></button>
              </div>
            </template>
            <p v-if="customerInfo.meterType == 0"
               style="float:left;padding:25px 20px 0 45px;height: 6.5em;font-size:0.83em">
               'DL/T645-1997'IC
            </p>
          </template>
        </div>
       </dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
    </div>
</template>
<script>
    import priceTable from '../../component/childComponent/priceTable.vue'
    import flash from  '../../component/childComponent/FlashPlugin.vue'
    export default{
        data () {
            return {
                showAlerts: false,
                payMsg: '',
                cursor: 'default',
                msg: '!', //
                payGuid: '',
                sign: -1, //-1 012 3
                saveMsg: false,
                showData: false,
                priceData: '',
                showPrice: false,
                selected: '',
                operateFlag: 1, //1   3 
                showDialog: false,
                allowWrite: 2,  //0 - 2-
                customerInfo: {
                    payment: 0
                    //cno  
                    //meterUserNo 
                    //payMoney
                },
                cardInfo: {
                    meterType: 2,//0- 1 0921313
                    changeRemark: '',
                    alertFee1: '50.00',
                    alertFee2: '0.00',
                    curTranfRto: 1,
                    volTranfRto: 1,
                    userType: 2,      //2 
                    userTypeText: '',
                    payCount: 1,
                    changeTime: "0000000000" //
                }
            }
        },
        props: [],
        methods: {
            mouseUp: function (e) {
                this.cursor = 'default';
                this.flag = false;
            },
            mouseMove: function (e) {
                let endX = e.clientX;
                let endY = e.clientY;
                let moveX = endX - this.startX;
                let moveY = endY - this.startY;
                let target = this.$el.lastChild;
                let width = document.body.clientWidth - target.offsetWidth;
                let height = document.body.clientHeight - target.offsetHeight;
                if (this.flag && parseInt(moveX) > 0 && parseInt(moveY) > 0 && parseInt(moveX) < width && parseInt(moveY) < height) {
                  //  this.styleParams.left = parseInt(moveX) + 'px';
                 //   this.styleParams.top = parseInt(moveY) + 'px';
                }
            },
            mouseDown: function (e) {
                this.cursor = 'move';
                this.flag = true;
            },
            getForm: function (val) {
               
                   this.getOption(val, true);
                
            },
            //
          //operate: 0:  1 2 3 
          //param: {customerNo, cno, payGuid}
          show: function (param, operate) {
            let that = this;
            that.showDialog = true;
            that.getInfo(param, operate);
            setTimeout(function () {
                that.$refs.openAccount.show({
                  hasClose:  !that.showData,
                  action: {
                      cancel: function () {
                          that.close();
                      }
                  }
                });
            }, 0)
          },
          //
          getInfo: function (param, operate) {
            // 012 3
            this.sign = operate;
            switch (operate) {
              case 0://
              case 3://
              case 1://
                this.operateFlag = 1;
                this.queryCstAcct(param.customerNo, param.cno);
                break;
              case 2://2
                this.operateFlag = 3;
                this.queryCstPayInfo(param.customerNo, param.cno, param.payGuid);
                break;
            }
          },
          //
          queryCstAcct: function (customerNo, cno) {
            let that = this;
            that.getRequest({
              url: 'icCard/queryAccount',
              method: 'post',
//              method: 'get',
//              url: 'dist/json/cardDetail.json',
              param: {customerNo: customerNo, cno: cno, flag: this.sign}, //Flag 3  
              success: function (data) {
                that.receiveData(data.data); //
              },
              error: function () {
                that.showData = false;
                that.showAlerts = true;
              }
            }, true)
          },
          //
          queryCstPayInfo: function (customerNo, cno, payGuid) {
            let that = this;
            if (typeof payGuid !== 'undefined') {
              this.payMsg = '';
              this.payGuid = payGuid;
            }
            //
            that.getRequest({
              url: 'icCard/queryRepectPay',
              method: 'post',
//              url: 'dist/json/reOpenAccount.json',
//              method: 'get',
              param: {
                customerNo: customerNo,
                cno: cno,
                payGuid: payGuid
              },
              success: function (data) {
                that.receiveData(data.data);
              },
              error: function () {
                that.showData = false;
                that.showAlerts = true;
              }
            }, true)
          },
          //
          receiveData: function (data) {
            let that = this;
            let Data = data.acctDetailInfo ? data.acctDetailInfo : data;
            for (let item in Data) {
              this.customerInfo[item] = Data[item];
            }
            //
            if (this.sign !== 0) {
              //
              this.cardInfo["payCount"] = this.customerInfo["payCount"];
              this.cardInfo["payMoney"] = this.customerInfo["payMoney"];
            }
            if (that.customerInfo.payment === null) {
              that.customerInfo.payment = 0;
            }
            //
            that.payGuid = Data.payGuid;

            //
            if (data.feePriceItemList && data.feePriceItemList.length !== 0) {
              that.priceData = data.feePriceItemList;
              that.showPrice = true;
            }
            else {
              that.showPrice = false;
            }
            //012 3:
            if (that.sign === 0 || that.sign === 3) {
              that.customerInfo.payMoney = parseFloat(0 - that.customerInfo.initAmount).toFixed(2);
            }
            if (that.sign === 1) {
              that.customerInfo.payMoney = parseFloat(that.customerInfo.payment - that.customerInfo.initAmount).toFixed(2);
            }
            that.showData = true;
          },
            //IC
            readCardClick: function () {
                this.loadings.show({
                    titleMsg: '',
                    msg: ',...',
                    action: {
                        cancel: function () {
                          disconnect();//
                        }
                     }
                });
                //
                bgFlashReadCard({
                    success: this.readSuccess,
                    callback: this.bgFlshCallback
                });
            },
            //
            readSuccess: function (data) {
                let that = this;
                let reCardData = JSON.parse(data);
                if (reCardData.data.electMeterNo === reCardData.data.lastDeviceNo) {
                    this.submitSave();
                }
                else {
                    that.alerts.show({msg: "!"});
                }
            },

            //IC
            writeCardClick: function () {
                if ((this.saveMsg && this.sign === 0) || this.sign > 0) {
                    if (this.customerInfo.meterType === null) {
                        this.loadings.close();
                        this.alerts.show({msg: '!'});
                        return;
                    }
                    if (this.allowWrite === 2) {
                        //
                        this.loadings.show({
                            msg: ',...',
                            action: {
                              cancel: function () {
                                disconnect();//
                              }
                            }
                        });
                        //
                        let writeData = this.cardFormParam.writeParam; //
                        for (let key in this.cardInfo) {
                            writeData[key] = this.cardInfo[key];
                        }
                        writeData.cardType = this.cardFormParam.operateFlag; //
                        writeData.electMeterNo = this.customerInfo.cno.substring(this.customerInfo.cno.length - 12, this.customerInfo.cno.length);//
                        writeData.customerID = this.padLeft(this.customerInfo.meterUserNo, 12); //
                        writeData.payMoney = parseFloat(writeData.payMoney, 10);
                        writeData.alertFee1 = parseFloat(writeData.alertFee1, 10);
                        writeData.alertFee2 = parseFloat(writeData.alertFee2, 10);
                        writeData.curTranfRto = this.customerInfo.ratio;
                        if (this.customerInfo.effectiveDate && this.customerInfo.effectiveDate !== "") {
                            //
                            let timeStr = this.customerInfo.effectiveDate.replace(/^20|-|\s|:/gi, "") + '0000';
                            writeData.changeTime = timeStr.substring(0, 10);
                        }

                        //IC
                        bgFlashWriteCard({
                            success: this.writeSuccess,
                            jsonObj: writeData,
                            meterType: this.customerInfo.meterType,
                            callback: this.bgFlshCallback
                        });
                    }
                    else {
                        this.loadings.close();
                        this.alerts.show({msg: ''});
                    }
                } else {
                    this.loadings.close();
                    this.alerts.show({msg: ''});
                }
            },
            //IC
            writeSuccess: function (data) {
                let that = this;
                this.allowWrite = 0; //
                this.getRequest({
                    url: 'icCard/update',
                    method: 'post',
                    param: {
                        payGuid: this.payGuid,
                        cno: this.customerInfo.cno,
                        customerNo: this.customerInfo.customerNo,
                        flag: this.sign == 1 ? 1 : 0
                    },
                    success: function (data) {
                      that.$emit('freshData');
                       let Data = data.data;
                      that.customerInfo['serialNum'] = Data['serialNum'];
                      that.customerInfo['payMethod'] = Data['payMethod'];
                      //
                      that.$refs.confirms.show({
                        msg: (that.sign == 2 ? '' : '') + '',
                        action: {
                          confirm: function () {
                            that.printPay();
                          },
                          cancel: function () {
                            that.close();
                          }
                        }
                      });
                    },
                  error: function (data) {
                    that.alerts.show({msg: data.message});
                    that.$emit('freshData');
                  }
                }, true)
            },
            //flash
            bgFlshCallback: function (msg) {
                this.loadings.close();
                if (typeof  msg !== 'undefined' && msg !== '') {
                    this.alerts.show({msg: msg});
                }
            },
            //
            printPay: function () {
                let that=this;
                this.loadings.show({
                    msg: ',...'
                });
                this.print({
                    title: "",
                    printContent: this.printVal,
                    callback: this.bgFlshCallback,
                    success:function(){
                        that.loadings.close();
                        that.close();
                    }
                });
            },
            //
            submitSave: function () {
                let that = this;
                if (isNaN(parseFloat(this.customerInfo['payment']))) {
                  this.alerts.show({
                    msg: ''
                  })
                } else {
                switch (this.sign) {
                    //
                    case 3://
                    case 0: //
                    {
                        let param = that.cardInfo;
                        param['payment'] = parseFloat(this.customerInfo['payment']);
                        param['payMoney'] = this.customerInfo.payMoney;

                        if (this.customerInfo.payMoney === null) {
                            that.alerts.show({msg: ""});
                            return;
                        }
                        if (param['payMoney'] < 0 || param['payMoney'] > 100000) {
                            that.alerts.show({msg: "0100000!"});
                            return;
                        }
                        param['meterType'] = that.customerInfo.meterType;
                        param['initAmount'] = that.customerInfo.initAmount;
                        param['customerNo'] = that.customerInfo.customerNo;
                        param['customerName'] = that.customerInfo.customerName;
                        param['customerContact'] = that.customerInfo.customerContact;
                        param['cno'] = that.customerInfo.cno;
                        param['curTranfRto'] = that.customerInfo.ratio;
                        //
                        that.loadings.show({
                            msg: ',...'
                        });

                        let submitUrl = '';
                        if (this.sign === 0) {
                            submitUrl = 'icCard/add';
                        }
                        else {
                            submitUrl = "icCard/reAdd";
                        }
                        //
                        that.getRequest({
                            url: submitUrl,
                            param: param,
                            method: 'post',
                            success: function (data) {
                              that.loadings.close();
                              that.$emit('getAccountInfo', param);
                              that.saveMsg = true;
                              that.payGuid = data.data.payGuid;
                              that.$set(that.customerInfo, 'payDate', data.data.createDate) ;
                              that.customerInfo.chargeUserName = data.data.chargeUserName;
                              that.writeCardClick();//IC
                            },
                          error: function (data) {
                            that.loadings.close();
                            that.alerts.show({msg: data.message});
                          }
                        });
                        break;
                    }
                    //
                    case 2: {
                        let param = {};
                        param['customerNo'] = that.customerInfo.customerNo;
                        param['cno'] = that.customerInfo.cno;
                        param['changeRemark'] = that.cardInfo.changeRemark;
                        //IC
                        that.cardInfo['payMoney'] = this.customerInfo.payMoney;
                       // console.log(that.cardInfo.changeRemark)
                        if(typeof that.cardInfo.changeRemark !== 'undefined' && that.cardInfo.changeRemark !== '') {
                           that.getRequest({
                            url: 'icCard/addChangeICCard',
                            param: param,
                            method: 'post',
                            success: function (data) {
                              that.saveMsg = true;
                              that.writeCardClick();//IC
                            },
                          error: function (data) {
                            that.alerts.show({
                              msg : data.message
                            });
                          }
                        });
                        } else {
                          that.alerts.show({
                              msg : ""
                            });
                        }
                        break;
                    }
                    //
                    case 1: {
                        that.saveMsg = true;
                        that.writeCardClick();
                        break;
                    }
                  }
                }
               
            },
            //
            close: function () {
                this.showAlerts = false;
                this.showDialog = false;
                this.showData = false;
                this.sign = -1; // -1 0 >0 
                this.priceData = '';
                this.cardInfo.changeRemark = '';
                this.customerInfo = {
                    payment: 0
                };
                this.allowWrite = 2;
                this.payGuid = '';
                this.$emit('refreshAgain')

            }
        },
        computed: {
            loadings: function () {
                return this.$refs.loadings
            },
            dialogFrom: function () {
                return [
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '',
                        value: this.customerInfo.customerName,
                        show: true
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        label: '',
                        show: true,
                        value: this.customerInfo.meterUserNo || '--'
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '',
                        show: true,
                        value: this.customerInfo.customerContact
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        label: '',
                        show: true,
                        value: this.customerInfo.cno !== null ? this.customerInfo.cno.substr(2).replace(/^0*/gi, '') : '--'
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '',
                        show: true,
                        value: this.getEnumItem('meterType', this.customerInfo.meterType).text || '--'
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        label: '',
                        show: this.operateFlag !== 3,
                        value: this.customerInfo.dictItemName
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '1',
                        show: this.operateFlag !== 3,
                        value: this.cardInfo.alertFee1
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        label: '2',
                        show: this.operateFlag !== 3,
                        value: this.cardInfo.alertFee2
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '',
                        show: this.operateFlag !== 3,
                        value: this.cardInfo.payCount
                    },
                    {
                        classes: 'halfWidth hasMarginLi',
                        type: 'input',
                        label: '',
                        show: this.operateFlag !== 3,
                        value: this.customerInfo.ratio
                    },
                    {
                        classes: 'halfWidth',
                        type: 'input',
                        label: '',
                        show: this.operateFlag !== 3,
                        value: this.cardInfo.volTranfRto
                    }
                ]
            },
            alerts: function () {
                return this.$refs.alerts
            },
            cardFormParam: function () {
                let data = {
                    allowWrite: this.allowWrite, // 0- 1- 2-
                    operateFlag: this.operateFlag, // 1 -  3-
                    writeParam: {
                        userType: this.cardInfo.userType,	//   01 02   2
                        paramUpdate: this.operateFlag === 1 ? 131 : 0,	// 0 0x83=131
                        alertFee1: parseFloat(this.cardInfo.alertFee1),	//1
                        alertFee2: parseFloat(this.cardInfo.alertFee2),	//2
                        curTranfRto: this.cardInfo.curTranfRto,	//
                        volTranfRto: this.cardInfo.volTranfRto,	//
                        electMeterNo: this.cardInfo.cno,	//
                        meterUserNo: this.cardInfo.meterUserNo,	//
                        cardType: this.operateFlag,	// 01   03 
                        payMoney: this.customerInfo.payMoney,	//
                        payCount: this.cardInfo.payCount,	//
                        changeTime: "0000000000",	//
                        oneRate1: 0.0,	//1
                        oneRate2: 0.0,	//2
                        oneRate3: 0.0,	//3
                        oneRate4: 0.0,	//4
                        oneRate5: 0.0,	//5
                        oneRate6: 0.0,	//6
                        oneRate7: 0.0,	//7
                        oneRate8: 0.0,	//8
                        oneRate9: 0.0,	//9
                        oneRate10: 0.0,	//10
                        oneRate11: 0.0,	//11
                        oneRate12: 0.0,	//12
                        twoRate1: 0.0,	//1
                        twoRate2: 0.0,	//2
                        twoRate3: 0.0,	//3
                        twoRate4: 0.0,	//4
                        twoRate5: 0.0,	//5
                        twoRate6: 0.0,	//6
                        twoRate7: 0.0,	//7
                        twoRate8: 0.0,	//8
                        twoRate9: 0.0,	//9
                        twoRate10: 0.0,	//10
                        twoRate11: 0.0,	//11
                        twoRate12: 0.0	//12
                    }
                };
                if (this.showPrice) {
                    for (let ix = 1; ix <= 12;) {
                        for (let i = 0; i < this.priceData.length; i++) {
                            data.writeParam['oneRate' + ix] = this.priceData[i].dataValue1;
                            data.writeParam['twoRate' + ix] = this.priceData[i].dataValue2;
                            ix++;
                        }
                    }
                }

                return data
            },
            inputOpt: function () {
                let width = 'calc(100% - 5em)';
                let payIsDisable;
                if (this.sign === 0 || this.sign === 3) {
                    payIsDisable = 'normalInput'
                } else {
                    payIsDisable = 'disabledInput'
                }
                return [
                    {
                        name: '',
                        classes: 'halfWidth',
                        isShow: this.operateFlag === 1,
                        style: 'input',
                        option: {
                            key: 'customerInfo.payment',
                            value: this.customerInfo.payment,
                             width: width,
                            type: payIsDisable,
                            noWatch: true,
                            length: 8,
                            limit: 'float'
                        }
                    },
                    {
                        name: '',
                        classes: 'halfWidth hasMarginLi',
                        isShow: this.operateFlag === 1,
                        style: 'input',
                        option: {
                            key: 'customerInfo.initAmount',
                            width: width,
                            value: this.customerInfo.initAmount,
                            type: 'disabledInput',
                            normal: true,
                            noWatch: true
                        }
                    },
                    {
                        name: '',
                        classes: 'halfWidth',
                        isShow: this.operateFlag === 1,
                        style: 'input',
                        option: {
                            key: 'customerInfo.payMoney',
                            value: this.customerInfo.payMoney,
                            width: width,
                            type: 'disabledInput',
                             normal: true,
                            noWatch: true
                        }
                    },
                    {
                        name: '',
                        classes: 'allWidth',
                        isEmpty: false,
                        isShow: this.operateFlag === 3,
                        style: 'input',
                        option: {
                            key: 'cardInfo.changeRemark',
                            width: width,
                            type: 'textArea',
                            regular: 'remark',
                            errorMsg: '',
                            noWatch: true
                        }
                    }
                ]
            },
            //
            printVal: function () {
                let cstName=this.customerInfo.customerName;
                let strTime=this.customerInfo.payDate;
                let deviceNo = this.customerInfo.cno.substring(this.customerInfo.cno.length - 12, this.customerInfo.cno.length);
                if(cstName.length>2){
                    cstName= this.customerInfo.customerName.replace(/^(.).+(.)$/g, "$1**$2")
                }
                else{
                    cstName= this.customerInfo.customerName.replace(/^(.).+$/g, "$1*")
                }
               
                if(strTime.length>0){
                    strTime=strTime.substring(0,16)
                }

                return [
                   {ItemType: 0, Filed: ":", Value:cstName , Align: 0},
                    {ItemType: 0, Filed: ":", Value: this.customerInfo.meterUserNo, Align: 0},
                    {ItemType: 0, Filed: ":", Value:deviceNo , Align: 0},
                    {ItemType: 0, Filed: ":", Value: this.customerInfo.customerAddr, Align: 0},
                    {ItemType: 0, Filed: ":", Value: "" + parseFloat(this.customerInfo.payMoney).toFixed(2), Align:0},
                    {ItemType: 0, Filed: ":", Value: this.customerInfo.payMethod, Align: 0},
                    {ItemType: 0, Filed: ":", Value: strTime, Align: 0},
                    {ItemType: 0, Filed: "  :", Value: this.customerInfo.chargeUserName.substring(0,1), Align: 0},
                    {ItemType: 0, Filed: "  :", Value: this.customerInfo.serialNum, Align: 0}
                ]
            }
        },
        components: {
            flash,
          priceTable
        },
        mounted: function () {

        },
        watch: {
            'customerInfo.payment': {
                handler(val, oldVal) {
                    if ((this.sign === 0 || this.sign === 3) && val !== oldVal && this.$refs[this.inputOpt[2].option.key] && this.$refs[this.inputOpt[0].option.key]) {
                        let money;
                        let payMoneyObj = this.$refs[this.inputOpt[2].option.key][0];
                        let payMentObj = this.$refs[this.inputOpt[0].option.key][0];
                        if (val !== '' && val !== null && typeof val !== 'undefined') {
                            let isRight = this.regularTestForVal({
                                val: val,
                                regex: 'float',
                                msg: ''
                            });
                            if (isRight) {
                                if (this.customerInfo.hasOwnProperty('initAmount')) {
                                    money = parseFloat(val) - parseFloat(this.customerInfo.initAmount);
                                    payMoneyObj.resetSearch(money.toFixed(2));
                                }

                            }
                            else {
                                payMentObj.resetSearch(null);
                                payMoneyObj.resetSearch(null);
                            }
                        } else if (val === '') {
                            payMentObj.resetSearch(null);
                            payMoneyObj.resetSearch(null);
                        }
                    }
                }
            }
        }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";

    .formBackIndex {
        z-index: 19
    }

    .formIndex {
        z-index: 20
    }

    .footerBtn {
        width: 100%;
        float: left;
        text-align: center;

    button {
        padding: 5px 20px;
        margin: 10px 5px;
    }

    .readCardBtn {
    //    background: $headerColor;
        color: white
    }

    }
    .footerBtn:after {
    @extend .clearElement
    }

    .operateFooter {
        position: absolute;
        bottom: 0;
        width: 100% !important;
        padding: 10px !important;
        margin: 0;
        left: 0;
        box-shadow: 0px -2px 3px #ccc;
    }

    .smallAlert {
        width: 420px !important;
        height: 120px !important;
    }

    .list-enter-active, .list-leave-active {
        transition: all 1s;
    }

    .list-enter, .list-leave-to {
        opacity: 0;
        transform: translateY(-90px);
    }
  .openAccount{
    .myDialogs .dialogWrapper .dialogContent .customerContent{
     // max-height: 15em
    }
  }
</style>



