<template>
    <div class="info-div" style="100%">
      <div class="info-header">
        <span class="callback" @click="resetComponent"> </span>
        <span> > </span>
      </div>
        <VuePerfectScrollbar class="info-container" v-if="show">
            <div v-for="(item, $index) in formOpt" class="list-content detail-content list-contentB" v-if="item.isShow">
                <div class="list-header">
                    <i class="hasIcon " :class="{hasImg: item.img}">
                      <icon :name="item.icon" v-if="item.icon"></icon>
                      <img :src="item.img" v-if="item.img" />
                    </i>
                    {{item.name}}
                    <template v-if="item.operate && item.lists.isShow && accountInfo.isEnabled != 0" v-for="(buttons, $index) in item.operate">
                       <!--&& controls[buttons.controls]-->
                        <button  v-if="accountInfo.isAccount == buttons.group && controls[buttons.controls]"
                                 class="list-operate"
                                 @click="btnEvent(buttons.event,buttons.value)">
                          <i class="hasIcon " :class="{hasImg: buttons.img}">
                            <icon :name="buttons.icon" v-if="buttons.icon"></icon>
                            <img :src="buttons.img" v-if="buttons.img" />
                          </i>
                          {{buttons.text}}
                        </button>
                    </template>
                </div>
                <ul v-if="item.listType === 'list'" class="form-list detail-list"  >
                    <li v-for="(items, $indexs) in lists[item.lists.data]" v-if="item.lists.isShow" >
                        <span class="label-span" style="min-width: 7.2em">{{items.name}} :&nbsp;&nbsp;</span>
                        <span  class="label-span" :style="{color: items.color || '', cursor: items.event ? 'pointer' : 'default',}" @click="getChangeInfo(items) ">{{getValue(items.value)}}</span>
                    </li>
                    <li v-if="accountInfo.isAccount == 0 && accountInfo.isEnabled != 0" style="padding-left:1em">
                        <template v-for="(infos, $index) in item.info" >
                            <span  >{{infos.msg}}</span>
                           <!--controls[infos.controls]-->
                            <button  style="font-size: 0.875em" v-if="controls[infos.controls]" class="list-operate" @click="btnEvent(infos.event, infos.value)" >
                              <i class="hasIcon"><icon :name="infos.icon"></icon></i>
                              {{infos.text}}
                            </button>
                        </template>
                    </li>
                    <li v-if="accountInfo.isEnabled == 0">
                        <template v-for="(infos, $index) in item.info" >
                            <span class="label-span " style="width:100px;text-align: left;margin-left:1em"></span>
                        </template>
                    </li>
                </ul>
                <div  v-if="item.listType === 'table'" style="margin: 1.5em 1em; " >
                    <priceTable v-if="showPrice" :width="'80%'" :priceData="priceData" ></priceTable>
                </div>
            </div>
        </VuePerfectScrollbar>
        <openCard ref="openCards" :selected="selected" @freshData="getInfoData"></openCard>
        <dialogs type="customer" ref="changeCard" v-if="showChange" width="50em" maxHeight="500px">
        <div slot="header">
          
        </div>
        <div slot="content">
            <tables :tableOpt="tableOpt" class="dialogTable"></tables>
        </div>
      </dialogs>
    </div>
</template>
<script>
    import priceTable from '../../childComponent/priceTable.vue'
    import openCard from '../../childComponent/OpenAccount.vue'
    import 'vue-awesome/icons/credit-card-alt'
    import 'vue-awesome/icons/credit-card'
    import 'vue-awesome/icons/address-card'
    export default{
        data() {
            return {
                showChange: false,
                show: true,
                priceData: '',
                selected: '',
                accountInfo: '',
                showPrice: false,
                pricePlan: '',
                formOpt: [
                    {
                        name: '',
                        img: 'dist/img/costControl/user-infor-icon.png',
                        listType: 'list',
                        isShow: true,
                        lists: {data: 'customerInfo', isShow: true}
                    },
                    {
                        name: 'IC',
                        img: 'dist/img/costControl/card-charging-button.png',
                        listType: 'list',
                        isShow: true,
                        operate: [
                            {text: '', event: 'operateCard',group:1,  value: 1, controls: 14, icon: 'credit-card-alt'},
                            {text: '', event: 'operateCard',group:2, value: 1, controls: 15, icon: 'credit-card'}
                           // {text: '', event: 'operateCard',group:2, value: 3, controls: 19, icon: 'address-card'}  //controls 
                        ],
                        lists: {data: 'cardInfo', isShow: false},
                        info: [
                            {
                                msg: '!!',
                                icon: 'credit-card-alt',
                                event: 'operateCard',
                                value: 0,
                                controls: 14,
                                text: ''
                            }
                        ]
                    },
                    {
                        name: '',
                        isShow: false,
                        img: 'dist/img/costControl/price-icon.png',
                        listType: 'table'
                    }
                ],
                tableOpt: {
                  queryParam: {},
                  column: [
                    {title: '', field: 'changeTime', textAlign: 'center', width: '60'},
                    {title: '', field: 'changeUserName', textAlign: 'center', width: '60'},
                    {title: '', field: 'changeRemark', textAlign: 'center', width: '140', type: 'string', formatter: function (val) {return val || '--'}}
                  ],
                  rowName: 'data',
                  url: 'icCard/queryChangeICCard',
                  method: 'post',
//                  method: 'get',
//                  url: 'dist/json/changeCard.json',
                  pageSize: 10,
                  pageNumber: 1,
                  pageList: [10,20,30],
                  height: 250
                }
            }
        },
        components: {
            openCard,
            priceTable
        },
        props: ['row', 'controls'],
        methods: {
            closeDialog: function () {
                this.$refs.changeCard.close();
                this.showChange = false;
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            btnEvent: function (eventName, val, sign) {
                this[eventName](val, sign);
            },

            operateCard: function (sign) {
                let that = this;
                setTimeout(function () {
                  that.$refs.openCards.show({
                    customerNo: that.selected.customerNo,
                    cno: that.selected.cno
                  }, sign)
                }, 0)
            },
            getChangeInfo: function (item) {
                if (item.event) {
                    this.tableOpt.queryParam =  {customerNo: this.selected.customerNo, cno: this.selected.cno};
                    this.showChange = true;
                    let that =  this;
                    setTimeout(function () {
                      that.$refs.changeCard.show({
                        hasClose: true,
                        action: {
                            cancel: function () {
                                that.closeDialog()
                            }
                        }
                      });
                    });

                }
            },
            getInfoData: function () {
                let param = {};
                param['customerNo'] = this.selected.customerNo;
                param['cno'] = this.selected.cno;
                let that = this;
                that.getRequest({
                    url: 'icCard/queryDetail',
                    method: 'post',
//                    url: 'dist/json/accountDetail.json',
//                    method: 'get',
                    param: param,
                    success: function (data) {
                      that.accountInfo = data.data.acctDetailInfo;
                      that.priceData = data.data.feePriceItemList;
                    }
                }, true)
            },
            init: function () {
                this.selected = this.row;
                this.getInfoData();
            }
        },
        computed: {
            lists: function () {
                let data =  this.accountInfo;
                if (this.accountInfo !== '' && typeof data.isAccount !== 'undefined' && data.isAccount !== 0 ) {
                    this.formOpt[1].isShow = true;
                    this.formOpt[1].lists.isShow = true;
                    this.formOpt[2].isShow = true;
                    this.showPrice = true;
                }
                return {
                    customerInfo: [
                        {name: '', value: this.accountInfo.customerName},
                        {name: '', value: this.accountInfo.propertyName},
                        {name: '', value: this.accountInfo.customerContact},
                        {name: '', value: this.accountInfo.dictItemName},
                        {name: '', value: this.accountInfo.deviceNo  ||  ''} ,
                        {name: '', value: this.accountInfo.meterUserNo},
                        {name: '', value: this.accountInfo.customerAddr}
                    ],

                    cardInfo: [
                        {name: '', value: this.accountInfo.payCount},
                        {name: '', value: this.accountInfo.payment, color: '#CC8E51'},
                        {name: '1', value: this.accountInfo.alertFee1, color: '#EB4954'},
                        {name: '2', value: this.accountInfo.alertFee2, color: '#EB4954'},
                        {name: '', value: this.accountInfo.overdraftFee, color: '#EEBF18'},
                        {name: '', value: this.accountInfo.cornerFee},
                        {name: '', value: this.accountInfo.ratio},
                        {name: '', value: this.accountInfo.volTranfRto},
                        {
                            name: '',
                            value: this.accountInfo.changeCount,
                            color: '#117CC8',
                            event: true
                        }
                    ]
                }
            }
        },
        mounted: function () {
            this.isIn();
            this.init();
            // this.getAction();
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    button.cardOperate {
        padding:5px 12px;
        margin:5px 10px;
        float:left
    }
</style>



