function openCon() {
  try {
    document.getElementById("JsSocket").openCon(877);
  } catch (e) {
    AlterMsg("Flash!<br /><a style='color:#69A4E7' href='" + bgSocketcCache.fileUrl + "' target='_blank'></a>");
  }
}

