function connectSuccess() {
  extendMsg("");

  //
  if (bgSocketcCache.querySendFunc != null && typeof bgSocketcCache.querySendFunc == 'function') {
    setTimeout(bgSocketcCache.querySendFunc, 100);
  } else {
    AlterMsg("NULL");
  }
}

