  for (var i = 0; i < factoryMapping.length; i++) {
    var row = factoryMapping[i];
    //
    if (row.factoryValue == factoryValue && (row.meterType == 0 || row.meterType > 0 && meterType == row.meterType)) {
      return row.dbType;
    }
  }
  //
  return defaultDbtype;
