<template>
    <div ref="trys" style="height:100%">
        <div v-if="showInfo" class="info-container">
            <info :row="selected" @closeDiv="closeDiv" :controls="controls"></info>
        </div>
        <div v-show="!showInfo" class="all-container">
            <div class="operate-div" ref="operateDiv">
                <template v-for="(item, $index) in FormOpt">
                    <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
                            @getInput="getFrom"></inputs>
                    <selects ref="selects" style="margin-left:10px" v-if="item.type == 'select'" :option="item.options"

                             @getSelect="getFrom"></selects>
                </template>
            </div>
          <!--controls[20]-->
          <div class="operate-div operateBtn" v-if="controls[20]">
            <button v-if="" class="button-operate" @click="openDialog">
              <i class="hasIcon hasImg"><icon name="wrench" style="font-size: 1.2em"></icon></i>IC
            </button>
          </div>
         <!--  <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
            <label></label>
            <template v-for="(data, $index) in deepSearchData">
                  <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}} {{data.value}}
                    <i v-if="data.flag" @click=" removeSearchData($index)">
                      <icon name="remove" scale="0.8"></icon>
                    </i>
                  </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"
                    @rowDbClick="clickTableRow"></tables>
          </div>
          <openCard ref="openCards"  @refreshAgain="getParams()"></openCard>
        </div>
      <dialogs type="customer" ref="cardCogs" width="46em" class="cogs" overflow = "auto" >
        <div slot="header">
          IC
        </div>
        <div slot="content">
          <table class="table-list" style="width: 90%;margin:0 5%;">
            <tr class="table-header">
              <th style="width: 100px"></th>
              <th style="width: 120px"></th>
            </tr>
            <tr v-for='item in cardTableRowData'>
              <td>{{item.name}}</td>
              <td>{{item.cardVal}}</td>
            </tr>
          </table>
        </div>
        <div slot="footer">
          <flash class="myFlash" style="margin-left:3em"></flash>
          <button class="button-default" type="button" @click="readCardClick" >
            <i class="hasIcon"><icon style="font-size:1.5em" name="indent"></icon></i>
            
          </button>
          <!--controls[20]-->
          <button class="button-default" v-if="controls[20]" @click="writeCardClick(1)">
            <i class="hasIcon"><icon  style="font-size:1.5em" name="outdent"></icon></i>
            
          </button>

          <!--<button class="button-default"-->
          <!--type="button"-->
          <!--v-on:click="writeCardClick(2)"><span class="glyphicon glyphicon-log-out"></span> IC[13]-->
          <!--</button>-->
        </div>
      </dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="confirm" ref="confirms"></dialogs>
    </div>
</template>
<script>
    import 'vue-awesome/icons/outdent'
    import 'vue-awesome/icons/indent'
    import 'vue-awesome/icons/wrench'
    import openCard from '../../childComponent/OpenAccount.vue'
    import '../../../../dist/plugin/bgsocket/bg.jssocket.js'
    import flash from  '../../../component/childComponent/FlashPlugin.vue'
    import info from './Info.vue'
    export default {
        data() {
            return {
              //  showClean: true,
                tableHeight: 'calc(100% - 4.5em)',
                searchParam: {
                   customerNo: '',
                   deviceType: '07',
                   status: '',
                   customerName: '',
                   customerContact: '',
                   meterUserNo: '',
                   deviceNo:'',
                   customerAddr: ''
                },
                deepSearchData: [],
                showInfo: false,
                controls: [],
                cardInfo: {
                    payMoney: 0,
                    alertFee1: 20,
                    alertFee2: 30,
                    payCount: 1,
                    curTranfRto: 1,
                    volTranfRto: 1,
                    dictItemValue: {text: '1', value: 1},
                    iCCardTyp: {text: 'IC', value: 1},
                    priceSolsCode: {text: '1', value: 1},
                    UpdateParam: {text: '1', value: 1}
                },
                searchType: 'customerName',
                selected: '', //
                TableOpt: {
                    url: 'icCard/queryList',
                    method: 'post',
//                    url: 'dist/json/account.json',
//                    method: 'get',
                    column: [
                        {
                            title: '',
                            field: 'isEnabled',
                            textAlign: 'center',
                            width: '130',
                            type: 'object',
                            formatter: function (val, index, row,obj) {
                            
                                let template;
                                if (row.isEnabled === 1) {
                                     return {
                                        template: '<div style="color: white;line-height: 2.3em" ></div>'
                                    };
                                } else {
                                    return {
                                        template: '<div style="color: red;line-height: 2.3em" ></div>'
                                    };
                                }
                            }
                        },
                        {title: '', field: 'customerName', textAlign: 'center', width: '120'},
                        {title: '', field: 'propertyName', textAlign: 'center',  width: 180},
                        {title: '', field: 'meterUserNo', textAlign: 'center', width: '130', sortable: false},
                        {
                            title: '',
                            field: 'cno',
                            textAlign: 'center',
                            sortable: false,
                            width: '160',
                            type: 'string',
                            formatter: function (val, index, row) {
                                if (val !== '' && val !== null) {
                                    return val.substr(2).replace(/^0*/gi, '')
                                }
                            }
                        },
                        {title: '', field: 'dictItemName', textAlign: 'center', width: '120'},
                        {
                            title: '',
                            field: 'isAccount',
                            textAlign: 'center',
                            width: '130',
                            type: 'object',
                            formatter: function (val, index, row,obj) {
                                //obj.$parent.controls[14] &&
                                let template;
                                if (obj.$parent.controls[14] && row.isEnabled === 1) {
                                    if (val === 0) {
                                        template = '<button class="list-operate"  @click="openAccount()"></button>';
                                    } else if (val === 1 || val === 2){
                                        template = '<div class="green" style="line-height: 2.3em;"></div>';
                                    } else {
                                        template = "<div style='line-height: 2.3em;'>--</div>";
                                    }
                                    return {
                                        template: template,
                                        methods: {
                                            openAccount: function () {
                                              obj.$parent.openCard(row, 0);
                                            }
                                        }
                                    }
                                } else {
                                	 if (val === 0) {
                                        template = "<div style='line-height: 2.3em;'></div>";
                                    } else if (val === 1 || val === 2){
                                        template = '<div class="green" style="line-height: 2.3em;"></div>';
                                    } else {
                                        template = "<div style='line-height: 2.3em;'>--</div>";
                                    }
                                    return {
                                        template: template
                                    };
                                }
                            }
                        },
                        {
                            title: '',
                            field: 'isAccount',
                            textAlign: 'center',
                            width: '130',
                            type: 'object',
                            formatter: function (val, index, row,obj) {
                                if( row.isEnabled===1) {
                                    let template;
                                    if (val > 0) {
                                        if (val > 1) {
                                            //controls[15]
                                            if(obj.$parent.controls[15]){
                                                template = '<button class="list-operate" @click="reAccount"></button>';
                                            }else {
                                                template = "<div style='line-height: 2.3em;'>--</div>";
                                            }
                                        }
                                        else {

                                            if(obj.$parent.controls[14]) {
                                                template = '<button class="list-operate"  @click="openAccount"></button>';
                                            }else {
                                                template = "<div style='line-height: 2.3em;'>--</div>";
                                           }
                                        }
                                        return {
                                            template: template,
                                            methods: {
                                                openAccount: function () {
                                                  obj.$parent.openCard(row, 1);
                                                },
                                                reAccount: function () {
                                                  obj.$parent.openCard(row, 1);
                                                }
                                            }
                                        }
                                    }
                                    else {
                                        return {
                                            template:  "<div style='line-height: 2.3em;'>--</div>"
                                        };
                                    }
                                }
                                else{
                                    return  {
                                        template:  "<div style='line-height: 2.3em;'>--</div>"
                                    };
                                }
                            }
                        },
                        {title: '', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
                        {title: '', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
                        {title: '', field: 'orgName', textAlign: 'center', width: '120'},
                        {
                            title: '', field: 'acctDatetime', textAlign: 'center', width: '170',
                            type: 'string',
                            formatter: function (val, index, row) {
                                if (row.isAccount === 0) {
                                    return '--';
                                }
                                else if(val!==null && val !=='') {
                                   return val.substr(0, 16)
                                }
                            }
                        }
                    ],
                    pageSize: 20,
                    pageNumber: 1,
                    queryParam: {deviceType: '07'},
                    pageList: [20, 40, 60],
                    sortName: 'acctDatetime',
                    sortOrder: 'desc'
                },
                //IC
                cardTableRowData: [{remark: '', name: '', cardVal: '-', key: 'customerName'},
                    {remark: '', name: '', cardVal: '-', key: 'customerContact'},
                    {remark: '', name: '',  cardVal: '-', key: 'customerAddr'},
                    {remark: '', name: '',  cardVal: '-', key: 'customerID'},
                    {remark: '', name: '',  cardVal: '-', key: 'electMeterNo'},
                    {remark: '', name: '',  cardVal: '-', key: 'payCount'},
                    {remark: '', name: '',  cardVal: '-', key: 'remainAmount'},
                    {remark: '', name: '',  cardVal: '-', key: 'overdraftFee'},
                    {remark: '', name: '',  cardVal: '-', key: 'curTranfRto'},
                    {remark: '', name: '',  cardVal: '-', key: 'volTranfRto'}
                ],
                //IC
                cardData:null,
                isReadCard:false,
            }
        },
        components: {
            openCard,
            info,
            flash,
        },
        methods: {
            //dialog
            openDialog: function () {
                let that = this;
            //    that.showClean = true;
                that.$nextTick(function () {
                  that.$refs.cardCogs.show({
                    hasClose: true,
                    action: {
                        cancel: function () {
                            that.closeDialog();
                        }
                    }
                  });
                })
            },
            //dialog
            closeDialog: function () {
                this.cardData = null;
                this.isReadCard = false;
               // this.showClean = false;
                this.$refs.cardCogs.close();
                this.initClearData(this.cardTableRowData, [{key: 'cardVal', value: '-'}]);
            },
            //
            initClearData: function (objData, keyVals) {
                objData.forEach(function (item) {
                    for (let i = 0; i < keyVals.length; i++) {
                        let keyval = keyVals[i];//
                        item[keyval.key] = keyval.value; //
                    }
                });
            },
            queryCustomer: function (param) {
                let that = this;
                let sParam = param.sParam;

                //
                this.getRequest({
                    url: 'pay/queryUser',
                    method: 'post',
                    param: sParam,
                    success: function (data) {
                      let datas = data.data;
                      that.cardTableRowData[0].cardVal = datas.customerName;
                      that.cardTableRowData[1].cardVal = datas.customerContact;
                      that.cardTableRowData[2].cardVal = datas.customerAddr;
                     // that.cardTableRowData[3].cardVal = datas.meterUserNo;
                    }
                });
            },
            //IC
            readCardClick: function () {
                //
                this.initClearData(this.cardTableRowData, [{key: 'cardVal', value: '-'}]);

                this.loadings.show({
                    msg: ',...',
                    action: {
                        cancel: function () {
                          disconnect();//
                        }
                    }
                });
                //
                bgFlashReadCard({
                    success: this.readSuccess,
                    callback: this.bgFlshCallback
                });
            },
            getTabColumn: function (key, dataObj) {
                let rowItem = null;
                dataObj.forEach(function (item) {
                    if (item.key === key) {
                        rowItem = item;
                        return true;
                    }
                });
                return rowItem;
            },
            //
            readSuccess: function (data) {
                let that = this;
                let reCardData = JSON.parse(data);

                this.cardData=reCardData.data;
                this.isReadCard=true;
                //
                let loadCardView = function () {
                    for (let key in reCardData.data) {
                        let rowItem = that.getTabColumn(key, that.cardTableRowData);
                        if (rowItem !== null) {
                            rowItem.cardVal = reCardData.data[key];
                            rowItem.remark = "--";
                        }
                    }
                };
                loadCardView();

                let sParam = {};
                sParam["deviceNo"] = reCardData.data.lastDeviceNo;

                //
                this.queryCustomer({
                    sParam: sParam
                });//
            },
            //IC
            writeCardClick: function (dbType) {
                let that = this;
                let runClear=function(){
                    let meterType=dbType;
                    if (that.cardData!==null && (/^0+$/gi.test(that.cardData.electMeterNo) && that.cardData.electMeterNo!==that.cardData.lastDeviceNo)) {
                        that.$refs.confirms.show({
                          msg: '?',
                          action: {
                            confirm: function () {
                              that.writeCard(meterType);
                            }
                          }
                        });
                    }
                    else {
                      that.$refs.confirms.show({
                        msg: '?',
                        action: {
                          confirm: function () {
                            that.writeCard(meterType);
                          }
                        }
                      });
                    }
                };
                if(this.isReadCard) {
                    runClear();
                }
                else{
                    this.alerts.show({msg: ''});
                }
            },
            //IC
            writeCard: function (meterType) {

                this.loadings.show({
                    msg: ',...'
                });
                //IC
                bgFlashClearCard({
                    success: this.writeSuccess,
                    callback: this.bgFlshCallback
                });
            },
            //IC
            writeSuccess: function (data) {
                this.alerts.show({msg: ''});
                this.cardData=null;
                this.isReadCard=false;
            },
            //flash
            bgFlshCallback: function (msg) {
                this.loadings.close();
                if (typeof  msg !== 'undefined' && msg !== '') {
                    this.alerts.show({msg: msg});
                }
            },
            removeSearchData: function (index) {
              let key = this.deepSearchData[index].key;
              if (key === 'status') {
                this.$refs['selects'][0].constant =  {text: '', value: ''};
                this.searchParam.status = '';
              } else  {
                this.searchParam[key] = this.$refs['inputs'][0].search = '';
                this.getParams(1);
              }
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
            },
            openCard: function (row, sign) {
                let that = this;
                setTimeout(function () {
                  let param = {
                    customerNo: row.customerNo,
                    cno: row.cno
                  };
                  that.$refs.openCards.show(param, sign);
                }, 0);

            },
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);
              let labelName;
              for (let key in arr) {
                let value = arr[key];
                let removeFlag = true;
                switch(key) {
                  case 'customerName':
                    labelName = '';
                    break;
                  case 'customerAddr':
                    labelName = '';
                    break;
                  case 'customerContact':
                    labelName = '';
                    break;
                  case 'meterUserNo':
                    labelName = '';
                    break;
                  case 'deviceNo':
                    labelName = '';
                    break;
                  case 'status':
                    labelName = '';
                    value = this.getEnumItem('accountStatus', value).text;
                    break;
                  case 'deviceType' :
                      labelName = '';
                      removeFlag = false;
                      value = this.getEnumItem('deviceType', value).text;
                      break;
                  default:
                    labelName = ''
                }
                this.deepSearchData.push({label: labelName, value: value, flag: removeFlag, key: key})
              }
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            clickTableRow: function (val) {
                this.selected = val.val;
                this.showInfo = true;
            },
            getFrom: function (val) {
              if (val.combine) {
                if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                  this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'status']);
                  this.getOption(val, true);
                  this.getParams(1);
               }
              } else {
                this.getOption(val, true);
              }
            },
            windowChange: function () {
              let that = this;
              that.getHeight();
              // window.onresize = function () {
              //   that.getHeight();
              // }
            },
            init: function () {
              let that = this;
              // window.onresize = function () {
              //   that.windowChange()
              // }
            }
        },
        computed: {
            loadings: function () {
                return this.$refs.loadings
            },
            alerts: function () {
                return this.$refs.alerts
            },
            FormOpt() {
            return [
//               {
//                 type: 'select',
//                 options: {
//                   cs: 'client',
//                   width: '7.5em',
//                   data: [{
//                       text: '',
//                       value: '07'
//                   }],
// //                  text: 'text',
// //                  value: 'value',
//                   key: 'searchParam.deviceType'
//                 }
//               },
              {
                type: 'select',
                options: {
                  cs: 'client',
                  width: '9em',
                  data: [
                    {text: '', value: ''},
                    {text: '', value: '1'},
                    {text: '', value: '0'},
                    {text: '', value: '-1'}
                  ],
                  key: 'searchParam.status',
                  constant: {value: ''}
                }
              },
              {
                type: 'input',
                options: {
                  type: 'typeSearch',
                  key: 'searchParam',
                  selectOpt: {
                    width: '7em',
                    cs: 'client',
                    data: [
                      {text: '', value: 'customerName'},
                      {text: '', value: 'customerContact'},
                      {text: '', value: 'meterUserNo'},
                      {text: '', value: 'deviceNo'},
                      {text: '', value: 'customerAddr'}
                    ],
                    text: 'text',
                    value: 'value',
                    key: 'searchType',
                    constant: {value: 'customerName'}
                  }
                }
              }
            ]
          }
        },
        mounted: function () {
            this.isIn();
            this.getAction();
        },
        watch: {
            'searchParam.status': {
                handler() {
                    this.getParams(1)
                }
            }
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    .cogs {
      .dialogWrapper .dialogContent .customerContent{
        //max-height: 18em!important;
      }
      .button-default{
        margin:1em!important
      }
    }
  .myFlash{
    margin-bottom:0.1em
  }

</style>



